@charset "utf-8";

section {
	width: 100%;
	height: auto;
	min-height: 900px;
	position: relative;
}
.loading_img {
	width: 390px;
	height: 844px;
	object-fit: cover;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
}

.movie_list {
	width: 1200px;
	margin: 0 auto;
}
.movie_list > h3 {
	text-align: center;
	padding: 50px 0;
	color: darkred;
}

.movie_list > ul {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	list-style: none;
}
.movie_list > ul > li {
	width: 25%;
	padding-bottom: 60px;
}
.movie_list > ul > li span {
	display: block;
	color: #787878;
	font-weight: 500;
}
.movie_list > ul > li h4 {
	width: 240px;
	font-size: 18px;
	color: #222;
	font-weight: 600;
}
.movie_list > ul > li p {
	color: #666;
}
