@charset "utf-8";
* {
	margin: 0;
	padding: 0;
}
.header {
	width: 100%;
	height: 80px;
	background: #ff0;
	text-align: center;
}
.header h1 {
	line-height: 80px;
}
.header h1 a {
	text-decoration: none;
	color: #222;
}
.footer {
	width: 100%;
	line-height: 40px;
	background: #333;
}

.footer address {
	color: #fff;
	font-style: normal;
	text-align: center;
}

main {
	width: 1200px;
	margin: 0 auto;
}

main,
section {
	margin-top: 50px;
}
