@charset "utf-8";

nav {
	width: 100%;
	height: 30px;
	background-color: #ff0;
}
nav > ul {
	width: 1200px;
	margin: 0 auto;
	display: flex;
	list-style: none;
	justify-content: space-between;
}
nav ul a {
	text-decoration: none;
	color: #333;
	font-weight: 500;
	position: relative;
}
nav ul a::after {
	content: '';
	position: absolute;
	width: 100%;
	height: 2px;
	background-color: #222;
	bottom: 0;
	left: 100%;
	opacity: 0;
	transition: 0.2s;
}

nav ul a:hover::after {
	left: 0;
	opacity: 1;
}
